<template>
    <div>
        <div class="popup__header popup__header--dotted">
            <div class="title">Добавление нового сотрудника</div>
        </div>

        <div class="popup__content">
            <div class="row-b">
                <input-multi
                        key="teamsFio"
                        id="teams-fio"
                        @chunk="setFio"
                        :is-error="fio.error"
                        v-model="fio.value"
                        :fields="fio.fields"
                        @focus="removeFieldError('fio')"
                        :placeholder="'ФИО сотрудника'"></input-multi>
            </div>
            
            
            <div class="row-b mt40">
                <div class="row-b__col elem-pos-fix mr24" style="width: 240px; min-width: 240px">
                    <input-styled
                            v-model="phone.value"
                            :mask="masks.phone"
                            :is-block-arrow-phone="true"
                            :is-error="phone.error"
                            @focus="removeFieldError('phone')"
                            :placeholder="'Номер телефона'"
                            :placeholder-input="'+7(___)___-__-__'"></input-styled>
                </div>

                <div id="company-ceo" class="row-b__col elem-pos-fit">
                    <input-styled
                            v-model="email.value"
                            :is-error="email.error"
                            :placeholder="'E-mail'"
                            @focus="removeFieldError('email')"
                            :placeholder-input="'info@domain.com'"></input-styled>
                </div>
            </div>
            
            
            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fit">
                    <div class="h2">Допуски и разрешения</div>
                </div>
            </div>
            

            <div class="mt16">
                <div class="mt16">
                    <input checked :disabled="true" type="checkbox" id="team-edit-0">
                    <label for="team-edit-0">Проверка и погашение купонов</label>
                </div>

                <div class="mt16" >
                    <input v-model="permission[5]"
                           type="checkbox"
                           :id="'team-edit-5'">
                    <label :for="'team-edit-5'">{{permissionText[5]}}</label>
                </div>

                <div class="mt16">
                    <input v-model="permission[6]"
                           type="checkbox"
                           :id="'team-edit-6'">
                    <label :for="'team-edit-6'">{{permissionText[6]}}</label>
                </div>

                <div class="mt16">
                    <input v-model="permission[16]"
                           type="checkbox"
                           :id="'team-edit-16'">
                    <label :for="'team-edit-16'">{{permissionText[16]}}</label>
                </div>

                <div class="mt16">
                    <input v-model="permission[7]"
                           type="checkbox"
                           :id="'team-edit-7'">
                    <label :for="'team-edit-7'">{{permissionText[7]}}</label>
                </div>

                <!--<div class="mt16">
                    <input v-model="permission[8]"
                           type="checkbox"
                           :id="'team-edit-8'">
                    <label :for="'team-edit-8'">{{permissionText[8]}}</label>
                </div>-->

                <div class="mt16">
                    <input v-model="permission[9]"
                           type="checkbox"
                           :id="'team-edit-9'">
                    <label :for="'team-edit-9'">{{permissionText[9]}}</label>
                </div>
            </div>

        </div>

        <div class="popup__footer">
            <div class="row-b">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn tag="button"
                                class="btn btn--green"
                                @click.native="save"
                                :type="stateBtn">
                        Добавить сотрудника
                    </submit-btn>
                </div>

                <div class="row-b__col elem-pos-fit">
                    <div class="dif">
                        <span class="i i-warning i--red mr8 mt-2"></span>
                        <span class="t text-color-red text-size-s">Не забудьте обучить нового сотрудника правилам погашения купонов<br> и правилам работы с покупателями!</span>
                    </div>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
    import InputStyled from '@/components/forms/InputStyled.vue'
    import { mapState } from 'vuex'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'
    import InputMulti from '@/components/forms/InputMulti.vue';

    export default {
        name: "TeamEdit",
        components: {
            InputStyled,
            InputMulti,
            SubmitBtn,
        },
        data() {
            return {
                permission: {
                    5: false,
                    6: false,
                    16: false,
                    7: false,
                    8: false,
                    9: false,
                },
                permissionText: {
                    5: 'Редактирование информации «О поставщике»',
                    6: 'Создание и редактирование акций',
                    16: 'Написание ответов на отзывы от лица компании',
                    7: 'Создание и редактирование сотрудников',
                    8: 'Просмотр статистики продаж',
                    9: 'Просмотр бухгалтерского отчета и статистики продаж',
                },
                stateBtn: 'normal',
                isSend: false,
                fio:{
                    disabled: false,
                    required: true,
                    value: '',
                    error: false,
                    errorMessage: '',
                    type: 'multi',
                    fields: {
                        lastName: {
                            name: 'lastName',
                            value: '',
                            delimiter: ' ',
                            placeholder: 'Фамилия',
                        },
                        name: {
                            name: 'name',
                            value: '',
                            delimiter: ' ',
                            placeholder: 'Имя',
                        },
                        secondName: {
                            name: 'secondName',
                            value: '',
                            delimiter: '',
                            placeholder: 'Отчество',
                        },
                    }
                },
                phone:{
                    disabled: false,
                    required: true,
                    value: '',
                    error: false,
                    type: 'phone',
                },
                email:{
                    disabled: false,
                    required: true,
                    value: '',
                    error: false,
                    type: 'email',
                }
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                currentUser: state => state.users.user,
                masks: state => state.masks.items,
            }),
        },
        mounted() {

        },
        methods: {
            save(){
                if(this.valid()){
                    this.stateBtn = 'load';
                    this.$store.dispatch('teams/add',{
                        permission:this.permission,
                        secondName:this.fio.fields.secondName.value,
                        name:this.fio.fields.name.value,
                        lastName:this.fio.fields.lastName.value,
                        phone:this.phone.value,
                        email:this.email.value,
                    }).then(()=>{
                        this.stateBtn = 'ok';
                        setTimeout(() => {
                            this.$store.commit('popups/close', {name: this.popups.userProviderAdd.name});
                            this.stateBtn = 'normal';
                        }, 300);
                    }, () => {
                        this.stateBtn = 'normal';
                    })
                }

            },
            valid(){
                let isCheck = true;
                if(!this.checkField(this.phone)){
                    isCheck  = false;
                }
                if(!this.checkField(this.email)){
                    isCheck  = false;
                }
                if(!this.checkField(this.fio)){
                    isCheck  = false;
                }
                return isCheck;
            },
            checkField(field){
                let isCheck = true;
                this.$set(field, 'error', false);
                switch (field.type) {

                    case 'phone':

                        let phone = field.value.replace(/[^\+0-9]/g, '');

                        if (phone.length < 12) {
                            setTimeout(()=>{
                                this.$set(field, 'error', true);
                            },150);

                            isCheck = false;
                        } else if (phone[2] != '9') {
                            isCheck = false;
                            setTimeout(()=>{
                                this.$set(field, 'error', true);
                            },150);
                             //если 1, то текст тоже красится
                        } else {
                            this.$set(field, 'error', false);
                        }
                        break;

                    case 'email':

                        let r = /^[\w\.\d-_]+@[\w\.\d-_]+\.\w{2,99}$/i;
                        if (!r.test(field.value)) {
                            setTimeout(()=>{
                                this.$set(field, 'error', true);
                            },150);

                            isCheck = false;
                        } else {
                            this.$set(field, 'error', false);
                        }
                        break;
                    case 'multi':
                        for(let fieldIndexMulti in field.fields) {
                            let fieldMulti = field.fields[fieldIndexMulti];
                            if(fieldMulti.value.length === 0){
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            }
                        }
                        break;
                }
                return isCheck;
            },
            setFio(field){
                if(field.value !== undefined && field.name !== undefined){
                    this.fio.fields[field.name].value = field.value;
                }
            },
            removeFieldError(name){
                this[name].error = false;
            },
        }
    }
</script>

<style scoped>
    .popup--styled .popup__content {
        padding-top: 40px;
    }
</style>