<template>
    <div>
        <div class="popup__header popup__header--dotted">
            <div class="title">{{ user.lastName }} {{ user.name }} {{ user.secondName }}</div>
            <div class="subtitle">{{ user.phone }}, {{ user.email }}</div>
        </div>

        <div class="popup__content">
            <div class="row-b mb16">
                <div class="row-b__col elem-pos-fit">
                    <div class="h2">Допуски и разрешения</div>
                </div>

                <div class="row-b__col elem-pos-fix">
                    <a v-if="user.id !== currentUser.id && !user.isNotBanned&& currentUser.permission.indexOf(7) !== -1" class="link-grey" href="javascript:void(0);"><span @click="unban" v-if="user.isBanned">Разблокировать сотрудника</span><span @click="ban" v-else>Заблокировать сотрудника</span></a>
                    <span v-if="user.id === currentUser.id && currentUser.permission.indexOf(7) !== -1" class="text-color-grey">Вы не можете у себя менять допуски и разрешения</span>
                    <span v-if="currentUser.permission.indexOf(7) === -1" class="text-color-grey"><span class="i i-lock3 i--dark-trans mr4"></span>У вас нет доступа к редактированию сотрудников</span>
                </div>
            </div>
            
            
            <div class="mt16">
                <div class="mt16">
                    <input checked :disabled="true" type="checkbox" id="team-edit-0">
                    <label for="team-edit-0">Проверка и погашение купонов</label>
                </div>
                <div class="mt16" >
                    <input :disabled="user.id === currentUser.id || currentUser.permission.indexOf(7) === -1" v-model="permission[5]"
                           type="checkbox"
                           :id="'team-edit-5'">
                    <label :for="'team-edit-5'">{{permissionText[5]}}</label>
                </div>

                <div class="mt16">
                    <input :disabled="user.id === currentUser.id || currentUser.permission.indexOf(7) === -1" v-model="permission[6]"
                           type="checkbox"
                           :id="'team-edit-6'">
                    <label :for="'team-edit-6'">{{permissionText[6]}}</label>
                </div>

                <div class="mt16">
                    <input :disabled="user.id === currentUser.id || currentUser.permission.indexOf(7) === -1" v-model="permission[16]"
                           type="checkbox"
                           :id="'team-edit-16'">
                    <label :for="'team-edit-16'">{{permissionText[16]}}</label>
                </div>

                <div class="mt16">
                    <input :disabled="user.id === currentUser.id || currentUser.permission.indexOf(7) === -1" v-model="permission[7]"
                           type="checkbox"
                           :id="'team-edit-7'">
                    <label :for="'team-edit-7'">{{permissionText[7]}}</label>
                </div>

                <!--<div class="mt16">
                    <input :disabled="user.id === currentUser.id || currentUser.permission.indexOf(7) === -1" v-model="permission[8]"
                           type="checkbox"
                           :id="'team-edit-8'">
                    <label :for="'team-edit-8'">{{permissionText[8]}}</label>
                </div>-->

                <div class="mt16">
                    <input :disabled="user.id === currentUser.id || currentUser.permission.indexOf(7) === -1" v-model="permission[9]"
                           type="checkbox"
                           :id="'team-edit-9'">
                    <label :for="'team-edit-9'">{{permissionText[9]}}</label>
                </div>
            </div>
        </div>



        <div class="popup__footer" v-if="user.id !== currentUser.id && currentUser.permission.indexOf(7) !== -1">
            <div class="row-b">
                <submit-btn tag="button"
                            class="btn btn--green"

                            @click.native="save"
                            :type="stateBtn">
                    Сохранить изменения
                </submit-btn>

            </div>


        </div>
    </div>
</template>

<script>
    import InputStyled from '@/components/forms/InputStyled.vue'
    import { mapState } from 'vuex'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "TeamEdit",
        components: {
            InputStyled,
            SubmitBtn,
        },
        props: {
            user: {
                default(){
                    return {}
                },
            }
        },
        data() {
            return {
                permission: {
                    5: false,
                    6: false,
                    16: false,
                    7: false,
                    8: false,
                    9: false,
                },
                permissionText: {
                    5: 'Редактирование информации «О поставщике»',
                    6: 'Создание и редактирование акций',
                    16: 'Написание ответов на отзывы от лица компании',
                    7: 'Создание и редактирование сотрудников',
                    8: 'Просмотр статистики продаж',
                    9: 'Просмотр бухгалтерского отчета и статистики продаж',
                },
                stateBtn: 'normal',
                isSend: false,
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                currentUser: state => state.users.user,
            }),
        },
        mounted() {
            for(let i in this.permission){
                if(+i !== 4){
                    if(this.user.permission.find(item => +item === +i)){
                        this.permission[i] = true;
                    } else {
                        this.permission[i] = false;
                    }
                }

            }
        },
        methods: {
            save(){
                this.stateBtn = 'load';
                this.$store.dispatch('teams/save',{permission:this.permission,id:this.user.id}).then(()=>{
                    this.stateBtn = 'ok';
                    setTimeout(() => {
                        this.$store.commit('popups/close', {name: this.popups.userProviderEdit.name});
                        this.stateBtn = 'normal';
                    }, 300);
                }, () => {
                    this.stateBtn = 'normal';
                })
            },
            ban(){
                if(!this.isSend){
                    this.isSend = true;
                    this.$store.dispatch('teams/ban',{id:this.user.id}).then(()=>{
                        this.isSend = false;
                    });
                }

            },
            unban(){
                if(!this.isSend){
                    this.isSend = true;
                    this.$store.dispatch('teams/unban',{id:this.user.id}).then(()=>{
                        this.isSend = false;
                    });
                }
            }
        }
    }
</script>

