<template>
    <div class="content">
        <div class="main-title">
            <div class="main-title__wrap main-title--nowrap">
                <div class="h1">Сотрудники</div>
            </div>

            <div class="main-title__right --icon" v-if="user.permission.indexOf(7) !== -1">
                <a @click.prevent="openAddPopup" href="" class="link-grey text-size-s link-icon text-bold" style="text-decoration: none">
                    <span class="i i-plus2" style="transform: scale(0.75)"></span>
                    <span class="text">ДОБАВИТЬ СОТРУДНИКА</span>
                </a>
            </div>
            <div class="main-title__right" v-if="user.permission.indexOf(7) === -1">
                <span class="text-color-grey"><span class="i i-lock3 i--dark-trans mr4"></span>У вас нет доступа к созданию и редактированию сотрудников</span>
            </div>
        </div>
        <section class="container">

            <div class="container__full">

                <div class="partner-workers">
                    <div class="item">
                        <div class="item__title">
                            <span class="item item--name">
                                <span class="b">ФИО сотрудника</span>
                            </span>

                            <span class="item item--paid">
                                <span class="b">погашено купонов</span>
                            </span>

                            <span class="item item--sales">
                                <span class="b">Объём продаж</span>
                            </span>

                            <span class="item item--disc">
                                <span class="b">предоставлено скидок</span>
                            </span>

                            <!-- для стрелки вниз удалить класс "up" -->
                            <span @click="setSort('cashier')" class="item item--received active " :class="{up:sort.cashier === 'up'}">
                                <span class="b">поступило в кассу</span>
                            </span>
                        </div>
                    </div>


                    <div class="item" v-for="user in sorteredTeam">
                        <a @click="openEditPopup(user)" class="item__link" :class="{' --blocked':user.isBanned}" href="javascript:void(0);">
                            <span class="item item--name">{{ user.lastName }} {{ user.name }} {{ user.secondName }} <span v-if="user.isBanned" class="i i-stop" style="transform: scale(0.75)"></span></span>
                            <span class="item item--paid">{{ user.coupons.extinguished }}</span>
                            <span class="item item--sales">{{ user.coupons.price }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                            <span class="item item--disc">{{ user.coupons.discountSum }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                            <span class="item item--received">{{ user.coupons.cashier }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                        </a>
                    </div>

                    <div class="item">
                        <div class="item__total" v-if="teams.users && teams.users.length > 1">
                            <span class="item item--name"><span class="b">ВСЕГО</span></span>
                            <span class="item item--paid"><span class="b">{{ teams.couponsInfoTotal.extinguished }} </span></span>
                            <span class="item item--sales"><span class="b">{{ teams.couponsInfoTotal.price }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span></span>
                            <span class="item item--disc"><span class="b">{{ teams.couponsInfoTotal.discountSum }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span></span>
                            <span class="item item--received"><span class="b">{{ teams.couponsInfoTotal.cashier }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span></span>
                        </div>
                    </div>
                </div>
                <!-- end buh-coupons -->

            </div>
            <!-- end container__full -->

        </section>
        <!-- end container -->
        <popup :is-set-styled="true" :show="popups.userProviderEdit.show" :name="popups.userProviderEdit.name">
            <team-edit :user="userEdit"></team-edit>
        </popup>
        <popup :is-set-styled="true" :show="popups.userProviderAdd.show" :name="popups.userProviderAdd.name">
            <team-add></team-add>
        </popup>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import Popup from '@/components/modal/Popup.vue';
    import TeamEdit from '@/components/modal/TeamEdit.vue';
    import TeamAdd from '@/components/modal/TeamAdd.vue';

    export default {
        metaInfo() {
            return {
                title: 'Сотрудники',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Team",
        components:{
            Popup,
            TeamEdit,
            TeamAdd
        },
        created() {
            this.$store.dispatch('teams/getItems');
        },
        mounted() {
        },
        data(){
            return {
                userEdit: {},
                sort:{
                    cashier: 'down'
                }
            }
        },
        computed: {
            ...mapState({
                teams:  state => state.teams.items,
                user: state => state.users.user,
                provider:  state => state.partner.current,
                popups: state => state.popups.items,
            }),
            sorteredTeam(){
                if(this.teams && this.teams.users){
                    let team = [...this.teams.users];
                    if(this.sort.cashier === 'down'){
                        return team.sort(function(a, b){
                            if(+(a.coupons.cashier.toString().split(' ').join('')) > +(b.coupons.cashier.toString().split(' ').join(''))){
                                return -1;
                            }
                            if(+(a.coupons.cashier.toString().split(' ').join('')) < +(b.coupons.cashier.toString().split(' ').join(''))){
                                return 1;
                            }
                            return 0;
                        });
                    }
                    else {
                        return team.sort(function(a, b){
                            if(+(a.coupons.cashier.toString().split(' ').join('')) < +(b.coupons.cashier.toString().split(' ').join(''))){
                                return -1;
                            }
                            if(+(a.coupons.cashier.toString().split(' ').join('')) > +(b.coupons.cashier.toString().split(' ').join(''))){
                                return 1;
                            }
                            return 0;
                        });
                    }
                } else {
                    return [];
                }

            }
        },
        methods: {
            openEditPopup(user){
                this.userEdit = user;
                this.$store.dispatch('popups/open', {name: this.popups.userProviderEdit.name});
            },
            openAddPopup(){
                this.$store.dispatch('popups/open', {name: this.popups.userProviderAdd.name});
            },
            setSort(type){
                this.sort[type] = this.sort[type] === 'up'? 'down':'up';
            }
        }
    }
</script>

